.App {
  text-align: center;
  background-color: #282c34;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  min-width: 100vw;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App > .button {
  background-color: slategray;
  border: 1px solid darkslategray;
  padding: 100px 0;
}

#question {
  margin-top: 0px;
}

#answer {
  margin-top: 2px;
}

.answered {
  display: flex;
  justify-content: space-evenly;
  margin-top: 4px;
  padding: 30px 0;
}

.show-answer {
  background-color: darkslategray;
  padding: 70px 0;
}